.temp {
  position: absolute;
  bottom: 0.1em;
  font-size: 7rem;
  font-weight: 400;
  margin-left: 0.4em;
  margin-bottom: 0.5em;
}

@media (max-width: 1100px) {
  .temp {
    font-size: 5rem;
    bottom: 2rem;
  }
}

@media (max-width: 768px) {
  .temp {
    font-size: 3.5rem;
    bottom: 5rem;
    left: 0.8rem;
  }
}
